import React, { useContext } from 'react'
import { get } from 'lodash'
import ChevronSmallDown from '@elliemae/ds-icons/ChevronSmallDown'
import DSButton, { BUTTON_VARIANT } from '@elliemae/ds-basic/Button'
import { DropDownContext } from '../Combobox'

const DropdownIndicator = ({
  innerRef, onKeyDown, options, theme, ...rest }) => {
  const c = useContext(DropDownContext)
  return (
    <DSButton
      aria-label='dropdown-indicator'
      buttonType='text'
      className={`dropdown-indicator ${get(rest, 'selectProps.menuIsOpen', false) && !c.inputInFocus ? 'active' : ''}`}
      icon={<ChevronSmallDown />}
      innerRef={innerRef}
      onClick={c.onClickDropdownIndicator}
      onKeyDown={onKeyDown}
      variant={get(rest, 'selectProps.menuIsOpen', false) && !c.inputInFocus ? BUTTON_VARIANT.ACTIVE : BUTTON_VARIANT.DEFAULT}
      {...rest}
    />
  )
}

export default DropdownIndicator
