import React from 'react'
import DSTruncatedTooltipText from "@elliemae/ds-basic/TruncatedTooltipText"
import { PopperPositions as Positions } from "@elliemae/ds-basic/Popper"

function MultiValueLabel({ children }) {
  return <DSTruncatedTooltipText tooltipPlacement={Positions.TOP} value={children} />
}

export default MultiValueLabel
