/* eslint-disable no-confusing-arrow */
/* eslint-disable import/no-cycle, max-lines */
/* eslint-disable no-unused-expressions */
import React, { useCallback, useState, useRef } from 'react';
import Select from 'react-select';
import { Manager } from 'react-popper';
import { withContentRect } from 'react-measure';
import { cx } from '@elliemae/ds-utilities/utils';
import theme from '@elliemae/ds-system/theme';
import { TooltipTextProvider } from '@elliemae/ds-basic/TruncatedTooltipText';
import withSelectStringValueConverter from '../v1/withSelectStringValueConverter';
import DropdownIndicator from './components/DropdownIndicator';
import SelectMenu from './components/SelectMenu';
import Control from './components/Control';
import ClearIndicator from './components/ClearIndicator';
import MenuList from './components/MenuList';
import MultiValueLabel from './components/MultiValueLabel';
import MultiValueRemove from './components/MultiValueRemove';
import SingleValueLabel from './components/SingleValueLabel';
import LoadingIndicator from './components/LoadingIndicator';
// import { IndicatorSeparator } from './components/IndicatorSeparator'
import { calculateWidth } from './components/calculateWidth';
import { ValueContainer } from './components/ValueContainer';

const blockName = 'em-ds-combobox';
const container = `${blockName}-container`;
const getValues = (option, isMulti, valueProperty) => {
  if (!option) return null;
  return isMulti
    ? option.map(mOption => mOption[valueProperty])
    : option[valueProperty];
};
export const DropDownContext = React.createContext();

const DSComboBox2 = ({
  hideSelectedOptions = false,
  autoFocus = false,
  className = '',
  hasError = false,
  onFocus = () => null,
  onBlur = () => null,
  onChange = () => null,
  // onKeyDown = () => null,
  filterOption = undefined,
  onClickDropdownIndicator = () => null,
  onInputKeyDown = () => null,
  onInputChange = () => null,
  isRtl = false,
  isFocused = undefined,
  isMulti = false,
  showAllOption = false,
  value = null,
  options = [],
  clearable = false,
  searchable = true,
  disabled = false,
  placeholder = '',
  valueProperty = 'value',
  labelProperty = 'label',
  loading = false,
  menuIsOpen = undefined,
  inlineMenu = false,
  components: customComponents = {},
  measureRef,
  contentRect,
  readOnly = false,
  returnValue = true,
  expandMenuToContainer = true,
  expandMenuOutsideContainer = false,
  customMenuItemOptions = {
    useTruncatedText: false,
    itemSize: 35,
  },
  containerProps = {},
  maxOptions,
  zIndex = 11,
  ...restPropsToCustomizeSelect
}) => {
  const select = useRef();
  const [inputInFocus, setFocus] = useState(false);
  const [menuOpen, setMenuOpen] = useState(menuIsOpen);
  const [isMultipleKeyPressed, setIsMultipleKeyPressed] = useState(false);

  const handleInputChange = useCallback((val, action) => {
    if (action.action === 'input-change') {
      setMenuOpen(true);
      if (!inlineMenu) onChange({ value: val, label: val }, action);
    }
    onInputChange(val, action);
  }, []);

  const handleOnKeyDown = useCallback(
    e => {
      if (event.key == 'Control') setIsMultipleKeyPressed(true);

      //If Ctrl + Enter are pressed, focus is set to the input and the menu closes
      if (isMultipleKeyPressed && event.key == 'Enter') {
        setFocus(true);
        setMenuOpen(false);
        setIsMultipleKeyPressed(false);
        e.preventDefault();
      }
    },
    [isMultipleKeyPressed],
  );

  const handleFocus = useCallback(e => {
    setFocus(true);
    if (!inlineMenu) setMenuOpen(true);
    onFocus(e);
  }, []);

  const handleBlur = useCallback(e => {
    setFocus(false);
    if (!inlineMenu) setMenuOpen(false);
    onBlur(e);
  }, []);

  return (
    <TooltipTextProvider>
      <DropDownContext.Provider
        value={{
          inputInFocus,
          value,
          isMulti,
          onClickDropdownIndicator,
          clearable,
          removeDropdownIndicator: customComponents.DropdownIndicator === null,
          select,
        }}
      >
        <Manager>
          <div
            ref={measureRef}
            className={cx(
              `${container}`,
              inputInFocus && 'in-focus',
              hasError && 'with-error',
              disabled && 'is-disabled',
              className,
            )}
            data-testid={'combobox'}
            {...containerProps}
          >
            <Select
              ref={select}
              autoFocus={autoFocus}
              classNamePrefix={blockName}
              // If it is multiselect let the menu open on select.
              closeMenuOnSelect={!isMulti}
              components={{
                Control,
                // IndicatorSeparator,
                Menu: SelectMenu,
                DropdownIndicator,
                ClearIndicator,
                MultiValueLabel,
                MultiValueRemove,
                SingleValue: SingleValueLabel,
                MenuList,
                ValueContainer,
                ...customComponents,
              }}
              customMenuItemOptions={{ maxOptions, ...customMenuItemOptions }}
              expandMenuOutsideContainer={expandMenuOutsideContainer}
              expandMenuToContainer={expandMenuToContainer}
              filterOption={filterOption}
              getOptionLabel={option => option[labelProperty]}
              getOptionValue={option => option[valueProperty]}
              hideSelectedOptions={hideSelectedOptions}
              inlineMenu={inlineMenu}
              isClearable={clearable}
              isDisabled={disabled || readOnly}
              isFocused={isFocused}
              isMulti={isMulti}
              isRtl={isRtl}
              isSearchable={readOnly ? false : searchable}
              menuIsOpen={menuOpen}
              noOptionsMessage={() => (loading ? <LoadingIndicator /> : null)}
              onBlur={handleBlur}
              onChange={
                returnValue
                  ? (option, action) => {
                      if (
                        showAllOption &&
                        isMulti &&
                        option.find(op => op.value === 'SELECT_ALL_COMBOBOX')
                      ) {
                        return onChange(
                          getValues(options, isMulti, valueProperty),
                          action,
                        );
                      }
                      isMulti ? setMenuOpen(true) : setMenuOpen(undefined);
                      return onChange(
                        getValues(option, isMulti, valueProperty),
                        action,
                      );
                    }
                  : (option, action) => {
                      if (
                        showAllOption &&
                        isMulti &&
                        option.find(op => op.value === 'SELECT_ALL_COMBOBOX')
                      ) {
                        return onChange(options, action);
                      }
                      isMulti ? setMenuOpen(true) : setMenuOpen(undefined);
                      return onChange(option, action);
                    }
              }
              onFocus={handleFocus}
              onInputChange={handleInputChange}
              onInputKeyDown={onInputKeyDown}
              onKeyDown={handleOnKeyDown}
              options={loading ? [] : options}
              placeholder={placeholder}
              selectMeasure={contentRect}
              styles={{
                control: () => null,
                option: () => null,
                dropdownIndicator: () => null,
                clearIndicator: () => null,
                placeholder: () => null,
                indicatorSeparator: () => null,
                singleValue: provided => ({
                  ...provided,
                  maxWidth: '90%',
                }),
                multiValueLabel: provided => ({
                  ...provided,
                  backgroundColor: 'transparent',
                }),
                multiValue: provided => ({
                  ...provided,
                  backgroundColor: 'transparent',
                }),
                multiValueRemove: provided => ({
                  ...provided,
                  '&:hover': {
                    backgroundColor: 'transparent',
                    color: theme.colors.neutral[800],
                  },
                }),
                valueContainer: provided => ({
                  ...provided,
                  flexWrap: 'nowrap',
                }),
                menu: () => {
                  if (expandMenuOutsideContainer) {
                    const width = calculateWidth(options);
                    if (!width) return null;
                    return {
                      width: `${width}px`,
                    };
                  }
                  if (loading) {
                    return {
                      height: '72px',
                    };
                  }
                  return null;
                },
              }}
              tabSelectsValue={false}
              value={value}
              zIndex={zIndex}
              {...restPropsToCustomizeSelect}
            />
          </div>
        </Manager>
      </DropDownContext.Provider>
    </TooltipTextProvider>
  );
};

export default withSelectStringValueConverter(
  withContentRect('bounds')(DSComboBox2),
);
